import React from 'react';
import jsPDF from 'jspdf';
import html2canvas from 'html2canvas';

class ThemesKPDKPBody extends React.Component {
  state = {
    position: 0
  }
  handleClick(state) {
    this.setState({
      position: state
    })
  }

  handleChange = (event) => {
    const {name, value} = event.target
    this.setState({
      [name]: value
    })
  };

  render() {
    console.log('kp-bkp themes', this.state);
    if (this.state.position == 0) {
      return (
        <section className="section">
          <div className="student-book-header-container">
            <div className="student-book-page-title"><h1 style={{paddingLeft: '0px'}}> /</h1></div>
          </div>
          <div className="content">
            <div style={{display: 'flex', flexDirection: 'row', backgroundColor: '#fff', borderRadius: '8px'}}>
              <div style={{
                width: '90%'
              }}>
                <p style={{textAlign:'left', color: 'rgba(0, 0, 0, 0.5)', marginLeft: '30px'}}> : <div style={{color: '#000', display:'inline-block'}}>2019/2020   4 </div></p>
                <p style={{textAlign:'left', color: 'rgba(0, 0, 0, 0.5)', marginLeft: '30px'}}> : <div style={{color: '#000', display:'inline-block'}}></div></p>
              </div>
              <div onClick={() => this.handleClick(1)} className="square_btn" style={{
                width: '10%',
                marginTop: '25px',
                marginBottom: '25px',
                marginRight: '30px'
              }}></div>
            </div>
          </div>
        </section>
      );
    } else if (this.state.position == 1) {
      return (
        <section className="section">
          <div className="student-book-header-container">
            <div className="student-book-page-title" style={{display: 'flex', flexDirection: 'row', width: '100%'}}>
              <h1 style={{paddingLeft: '0px', width: '90%'}}> /</h1>
              <div onClick={() => this.handleClick(2)} className="square_btn" style={{
                width: '10%',
                margin: 'auto'
              }}></div>
            </div>
          </div>
          <div className="content">
            <p style={{textAlign: 'left', color: '#8E8E93', fontSize: '16px'}}></p>
            <input
              type="text"
              name="theme"
              onChange={this.handleChange}
              style={{
                backgroundColor: 'transparent',
                border: '1px solid #0579FF',
                height: '40px',
                width: '100%',
                borderRadius: '8px',
                fontSize: '12px',
                textIndent: '20px'
              }}
            />
          </div>
        </section>
      );
    } else if (this.state.position == 2) {
      return (
        <section className="section">
          <div className="student-book-header-container">
            <div className="student-book-page-title" style={{display: 'flex', flexDirection: 'row', width: '100%'}}>
              <h1 style={{paddingLeft: '0px'}}> /</h1>
            </div>
          </div>
          <div className="content">
            <div style={{backgroundColor: '#fff', borderRadius: '8px', color: '#0579FF', textAlign: 'left', paddingLeft: '21px'}}>
              <p>  : {this.state.theme}</p>
            </div>
          </div>
        </section>
      );
    }
    return <div></div>;
  }
}

export default ThemesKPDKPBody;
