import React from 'react';
import NewsPiece from './news/news_piece.js';
import Event from './events/event.js';
import Email from './email/email.js';
import { loadEvents, handleError, getHost } from './data-loader.js'

class Body extends React.Component {
  state = {
    news: null,
    eventsMap: null
  };

  componentDidMount () {
    fetchNews().then(result => {
      console.log('result - ', result);
      this.setState({news: result});
    });
    handleError(
      loadEvents((events) => {
        let eventsMap = new Map([]);
        console.log('events - ', events);
        events.forEach(item => {
          if (eventsMap.has(item.course_id)) {
            eventsMap.get(item.course_id).push(item);
          } else {
            let items = [];
            items.push(item);
            eventsMap.set(item.course_id, items);
          }
        });
        this.setState({
          eventsMap: eventsMap
        });

      })
    )
  }

  render() {
    console.log("body props - ", this.props);
    if (!this.state.eventsMap) {
      return <div></div>;
    }
    return (
      <section className="section">
        <div className="content">
          <h1 className="page-title"></h1>
          <div>
            <div className="cards-header"><h2></h2></div>
            <div className="cards-link-see-all"><a href="" style={{display: 'none'}}> </a></div>
            <div className="cards">
              {this.state.news && this.state.news.slice(0, 3)}
            </div>
          </div>
          <div>
            <div className="cards-header"><h2></h2></div>
            <div className="cards-link-see-all"><a href="" style={{display: 'none'}}> </a></div>
            <div className="cards">
              {events(this.state.eventsMap, this.props.auth.user.course_id)}
            </div>
          </div>
          <div>
            <div className="cards-header"><h2></h2></div>
            <div className="cards-link-see-all"><a href="/email"> </a></div>
            <div className="cards">
              <Email
                  author=" .."
                  status="NEW"
                  time="19  2019, 10:49:44"
                  title="   "
                  content=" ,   &quot;&quot;    9    .  ,  ..      "
              ></Email>
              <Email
                  author=" .."
                  status="OLD"
                  time="28  2019, 13:08:28"
                  title="   30 "
                  content=" ,            ,      - 30 .         .  ,  ..      "
              ></Email>
              <Email
                  author=" .."
                  status="OLD"
                  time="20  2019, 15:16:07"
                  title="      "
                  content="  ,             (3  4 ).          6  -      .       .  ,  ..      "
              ></Email>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

function events(eventsMap, courseNumber) {
  let events = eventsMap.get(courseNumber);
  return events.map(event => <Event
        header={event.title}
        content={event.description}
        type={event.tag.toUpperCase()}
        image={event.imageUrl}
    />);
}

async function fetchNews() {
  return fetch(getHost() + '/railsapp/news_pieces.json')
    .then(response => response.json())
    .then(data => {
      console.log('data - ', data);
      return data.map(it => <NewsPiece
            title={it.title}
            time={timeSince(new Date(it.time))}
            content={it.description}
          ></NewsPiece>);
    });
}

function timeSince(date) {

  var seconds = Math.floor((new Date() - date) / 1000);

  var interval = Math.floor(seconds / 31536000);

  if (interval > 1) {
    return interval + " ";
  }
  interval = Math.floor(seconds / 2592000);
  if (interval > 1) {
    return interval + " ";
  }
  interval = Math.floor(seconds / 86400);
  if (interval > 1) {
    return interval + " ";
  }
  interval = Math.floor(seconds / 3600);
  if (interval > 1) {
    return interval + " ";
  }
  interval = Math.floor(seconds / 60);
  if (interval > 1) {
    return interval + " ";
  }
  return Math.floor(seconds) + " ";
}

export default Body;
